ul.menu {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin: 0.5vw 0 0 0;
    padding: 0;

    li {
        list-style: none;
        margin: 0.5vw;

        a {
            padding: 1vw;
            text-decoration: none;
            color: #6893b7;
            font-size: 1.8rem;
            border: none;

            &:hover {
                color: #6893b7;
                background-color: #333333dd;
                background: linear-gradient(0, rgba(51,51,51,0.5) 0%, rgba(51,51,51,0) 100%);
            }

            span {
                text-decoration: none;
                color: #6893b7;
                font-size: 2.2rem;
                border-bottom: 1px solid peru;
            }
        }
    }
}